html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: #0D192C;
  font-family: "Outfit", system-ui;
}

.card {
  width: 35rem;
  height: 62rem;
  background-color: #15263F;
  margin: 0 auto;
  margin-top: 10rem;
  margin-bottom: 5rem;
}
.card .imagen {
  position: relative;
  display: inline-block;
}
.card .imagen .nft {
  width: 30.2rem;
  height: 30.2rem;
  margin: 2.4rem 2.4rem 0 2.4rem;
  position: relative;
  z-index: 1;
}
.card .imagen::before {
  width: 30.2rem;
  height: 30.2rem;
  margin: 2.4rem 2.4rem 0 2.4rem;
  border-radius: 0.8rem;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #00FFF8;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  z-index: 2;
  cursor: pointer;
}
.card .imagen .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 44px;
  height: 33px;
  z-index: 3;
  background-image: url("../img/icon-view.svg");
  background-position: center;
  background-blend-mode: normal;
  background-repeat: no-repeat;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}
.card .imagen:hover::before {
  opacity: 0.6;
}
.card .imagen:hover .icon {
  opacity: 1;
}
.card .titulo {
  font-size: 2.2rem;
  font-weight: 600;
  text-align: left;
  margin-left: 2.4rem;
  cursor: pointer;
}
.card .titulo:hover {
  color: #00FFF8;
}
.card .texto {
  font-weight: 300;
  font-size: 1.8rem;
  line-height: 26px;
  margin-left: 2.4rem;
}
.card .one {
  display: flex;
  align-items: center;
  margin-left: 2.4rem;
  gap: 0.7rem;
}
.card .one .eterum-text,
.card .one .reloj-texto {
  font-size: 1.3rem;
  font-weight: 600;
}
.card .one .reloj {
  margin-left: 13rem;
}
.card .barra {
  width: 30rem;
  height: 0.1rem;
  background-color: #2E405A;
  margin-top: 2rem;
  margin-left: 2.4rem;
}
.card .footer {
  display: flex;
  align-items: center;
  margin-top: 2rem;
  margin-left: 2.4rem;
}
.card .footer .avatar {
  width: 3.3rem;
  height: 3.3rem;
  border-radius: 50%;
  border: 1px solid #FFFFFF;
}
.card .footer .textoImg {
  font-size: 1.8rem;
  font-weight: 400;
  margin-left: 2rem;
  cursor: pointer;
}
.card .footer .span:hover {
  color: #00FFF8;
}

.color-white {
  color: #FFFFFF;
}

.br-radius {
  border-radius: 1.5rem;
}

.br-radius-img {
  border-radius: 0.8rem;
}

.colorParrafo {
  color: #8BACD9;
}

.eterum-text {
  color: #00FFF8;
}